<template>
  <div class="history clear">
    <div class="history1 left">
      <p>Winning Numbers <b>20171220180</b></p>
      <p class="txt-center">
        <span class="ball-num" v-for="ele in winNums" :key="ele">{{ele}}</span>
      </p>
      <p class="txt-center">Current Estimated Jackpot:</p>
      <h3 class="txt-center">$1985987</h3>
    </div>
    <div class="history2 left">
      <p>Next Lottery Time: <b>12/21/2017 6:09 AM</b></p>
      <p class="txt-center">
        <span class="time-span">8</span>
        <span class="time-span">8</span>
        <span class="time-span">8</span>
        <span class="time-span">8</span>
      </p>
      <p class="divider"></p>
      <img src="../assets/images/index2.png">
      <img src="../assets/images/index3.png">
      <div class="related-info">
        <p><a class="left" href="">　Related Information</a><a class="right" href=""> More</a></p>
        <div>
          <ul>
            <li><a href="">1</a></li>
            <li><a href="">2</a></li>
            <li><a href="">3</a></li>
          </ul>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
  export default {
    computed: {
      winNums () {
        return [3, 8, 9, 4, 6, 1, 7, 2, 10, 5];
      }
    }
  };
</script>
<style scoped>
  .history {
    margin: 0 auto;
    width: 1200px;
  }

  .history1, .history2 {
    box-sizing: border-box;
  }

  .history1 {
    width: 800px;
  }

  .history2 {
    width: 400px;
  }

  .clear:after {
    content: '';
    display: table;
    clear: both;
  }

  .divider {
    margin-top: 10px;
    border-bottom: 1px dotted #ddd;
  }
</style>



