<template>
  <header>
    <div class="header-content">
      <a href="#" class="logo">
        <img src="../assets/images/logo.png">
      </a>
      <ul class="nav">
        <li @click="linkTo('/')">
          <a href="#"><img width="26" src="../assets/images/home.png"> 首页</a>
        </li>
        <li @click="linkTo('/about')">
          <a href="#"><img width="24" src="../assets/images/help.png"> 关于火箭</a>
        </li>
      </ul>
    </div>
  </header>
</template>
<script>
  export default {
    methods: {
      linkTo (path) {
        this.$router.push(path);
      }
    }
  };
</script>
<style scoped>
  header {
    width: 100%;
    background-color: #002951;
    margin-bottom: 10px;
  }

  .header-content {
    height: 116px;
    position: relative;
    width: 1200px;
    margin: 0 auto;
  }

  .logo, .nav {
    float: left;
  }

  .logo {
    margin-top: 10px;
  }

  .nav li {
    float: left;
  }

  .nav {
    position: absolute;
    left: 800px;
    bottom: 0;
  }

  .nav > li {
    margin-left: 6px;
  }

  .nav a {
    text-decoration: none;
    color: #fff;
    display: inline-block;
    text-align: center;
    line-height: 100px;
    border-radius: 6px 6px 0 0;
    font-size: 1.5em;
    padding-right: 40px;
  }
</style>



