<template>
  <div class="index-main">
    <div class="index-carousel">
      <el-carousel :interval="4000" type="card">
        <el-carousel-item v-for="item in 3" :key="item">
          <img :src="`../static/public/images/lottery${item}.jpg`">
        </el-carousel-item>
      </el-carousel>
    </div>
    <p class=" index-border alarm">欢迎光临火箭娱乐，尊享高奖金高赔率多彩种之体验！</p>
    <div class="index-middle">

      <div class="middle-container">
        <a>火箭分分彩</a>
        <div class="leftright2">
          <div class="left1">
            <p>
              <img src="../assets/images/icon.png">
              <span class="seriod">No. {{Timer.customssc && Timer.customssc.currentPeriod-1}}</span>
            </p>
            <div>
              <span v-for="ele in historyNum && historyNum.customssc[0] && historyNum.customssc[0].num.split(',')">{{ele}}</span>
            </div>
          </div>
          <div class="right1">
            <h2>下期开奖</h2>
            <h3>No.{{Timer.customssc && Timer.customssc.currentPeriod}}</h3>
            <span>{{timeText.customssc}}</span>
          </div>
        </div>
        <span class="el-icon-arrow-down history-hand" v-if="autoHeight.a1==='height0'"
              @click="queryHistory('a1')" title="开奖历史"></span>
        <span class="el-icon-arrow-down history-hand" @click="queryHistory('a1')" v-else></span>
        <el-collapse-transition>
          <!--<div :class="autoHeight.a1">-->
          <div v-show="autoHeight.a1==='height-auto'">
            <hr/>
            <el-row type="flex">
              <el-col :span="8">期号</el-col>
              <el-col :span="8">时间</el-col>
              <el-col :span="8">中奖号码</el-col>
            </el-row>
            <el-row type="flex" v-for="ele in historyNum.customssc">
              <el-col :span="8">{{ele.period}}</el-col>
              <el-col :span="8">{{ele.lotTime | dateFormat('yyyy-MM-dd H:m:ss')}}</el-col>
              <el-col :span="8">
                <span class="ball" v-for="nu in ele && ele.num.split(',')">{{nu}}</span>
              </el-col>
            </el-row>
          </div>
        </el-collapse-transition>
      </div>

      <div class="middle-container airship">
        <a>飞速赛车</a>
        <div class="leftright2">
          <div class="left1">
            <p>
              <img src="../assets/images/icon.png">
              <span class="seriod">No. {{Timer.fastpk10 && Timer.fastpk10.currentPeriod-1}}</span>
            </p>
            <div>
              <span
                v-for="ele in historyNum && historyNum.fastpk10[0] && historyNum.fastpk10[0].num.split(',')">{{ele}}</span>
            </div>
          </div>
          <div class="right1">
            <h2>下期开奖</h2>
            <h3>{{Timer.fastpk10 && Timer.fastpk10.currentPeriod}}</h3>
            <span>{{timeText.fastpk10}}</span>
          </div>
        </div>
        <span class="el-icon-arrow-down history-hand" v-if="autoHeight.a2==='height0'"
              @click="queryHistory('a2')" title="开奖历史"></span>
        <span class="el-icon-arrow-down history-hand" @click="queryHistory('a2')" v-else></span>
        <el-collapse-transition>
          <div v-show="autoHeight.a2==='height-auto'">
            <hr/>
            <el-row type="flex">
              <el-col :span="8">期号</el-col>
              <el-col :span="8">时间</el-col>
              <el-col :span="8">中奖号码</el-col>
            </el-row>
            <el-row type="flex" v-for="ele in historyNum.fastpk10">
              <el-col :span="8">{{ele.period}}</el-col>
              <el-col :span="8">{{ele.lotTime | dateFormat('yyyy-MM-dd H:m:ss')}}</el-col>
              <el-col :span="8">
                <span class="ball" v-for="nu in ele && ele.num.split(',')">{{nu}}</span>
              </el-col>
            </el-row>
          </div>
        </el-collapse-transition>
      </div>

      <div class="middle-container">
        <a>火箭二分彩</a>
        <div class="leftright2">
          <div class="left1">
            <p>
              <img src="../assets/images/icon.png">
              <span class="seriod">No. {{Timer.bestbetssc && Timer.bestbetssc.currentPeriod-1}}</span>
            </p>
            <div>
              <span
                v-for="ele in historyNum && historyNum.bestbetssc[0] && historyNum.bestbetssc[0] && historyNum.bestbetssc[0].num.split(',')">{{ele}}</span>
            </div>
          </div>
          <div class="right1">
            <h2>下期开奖</h2>
            <h3>No.{{Timer.bestbetssc && Timer.bestbetssc.currentPeriod}}</h3>
            <span>{{timeText.bestbetssc}}</span>
          </div>
        </div>
        <span class="el-icon-arrow-down history-hand" v-if="autoHeight.a3==='height0'"
              @click="queryHistory('a3')" title="开奖历史"></span>
        <span class="el-icon-arrow-down history-hand" @click="queryHistory('a3')" v-else></span>
        <el-collapse-transition>
          <div v-show="autoHeight.a3==='height-auto'">
            <hr/>
            <el-row type="flex">
              <el-col :span="8">期号</el-col>
              <el-col :span="8">时间</el-col>
              <el-col :span="8">中奖号码</el-col>
            </el-row>
            <el-row type="flex" v-for="ele in historyNum.bestbetssc">
              <el-col :span="8">{{ele.period}}</el-col>
              <el-col :span="8">{{ele.lotTime | dateFormat('yyyy-MM-dd H:m:ss')}}</el-col>
              <el-col :span="8">
                <span class="ball" v-for="nu in ele && ele.num.split(',')">{{nu}}</span>
              </el-col>
            </el-row>
          </div>
        </el-collapse-transition>
      </div>

    </div>
  </div>
</template>
<script>
  import config from '../api/config';

  export default {
    data () {
      return {
        timer: null,
        searchContent: '',
        timeText: {
          customssc: '00:00',
          fastpk10: '00:00',
          bestbetssc: '00:00'
        },
        autoHeight: {
          a1: 'height0',
          a2: 'height0',
          a3: 'height0'
        },
        Timer: {
          customssc: null,
          fastpk10: null,
          bestbetssc: null
        },
        historyNum: {
          fastpk10: [], // 赛车
          customssc: [], // 分分彩
          bestbetssc: [] // 二分彩
        },
        timers: {
          customssc: null,
          fastpk10: null,
          bestbetssc: null
        },
        lastLotteryTimers: {
          customssc: null,
          fastpk10: null,
          bestbetssc: null
        }
      };
    },
    computed: {
      winNums () {
        return [3, 8, 9, 4, 6, 1, 7, 2, 10, 5];
      },
      tableData () {
        return [
          {
            stage: 20171220180,
            num: '3 8 9 4 6 1 7 2 10 5 '
          },
          {
            stage: 20171220180,
            num: '3 8 9 4 6 1 7 2 10 5 '
          },
          {
            stage: 20171220180,
            num: '3 8 9 4 6 1 7 2 10 5 '
          }];
      }
    },
    created () {
      this.initLottery('customssc');
      this.initLottery('fastpk10');
      this.initLottery('bestbetssc');
    },
    methods: {
      queryHistory (which) {
        if (this.autoHeight[which] === 'height0') {
          this.autoHeight[which] = 'height-auto';
        } else {
          this.autoHeight[which] = 'height0';
        }
      },
      getTimer (type) {
        this.axios.get(`/gamelottery/currentSale.json?type=${type}`, config).then((res) => {
          if (res.data.code === 200) {
            this.Timer[type] = res.data.data;
            this.inittimer(type);
          }
        });
      },
      inittimer: function (type) {
        let intDiff = this.Timer[type]['nextStartTime'] / 1000;
        this.timers[type] = window.setInterval(() => {
          let day = 0;
          let hour = 0;
          let minute = 0;
          let second = 0;  // 时间默认值
          intDiff--;
          if (intDiff >= 0) {
            day = Math.floor(intDiff / (60 * 60 * 24));
            hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
            minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
            second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
            if (hour <= 9) hour = '0' + hour;
            if (minute <= 9) minute = '0' + minute;
            if (second <= 9) second = '0' + second;
            let html = minute + ':' + second;
            this.timeText[type] = html;
          } else {
            clearInterval(this.timers[type]);
            this.initLottery(type);
            this.getLastLottery(type);
          }
        }, 1000);
      },
      getLotteryHistory (type) {
        this.axios.get(`/gamelottery/top7.json?type=${type}`, config).then((res) => {
          if (res.data.code === 200) {
            this.historyNum[type] = res.data.data;
          }
        });
      },
      getLastLottery (type) {
        this.lastLotteryTimers[type] = setInterval(() => {
          this.axios.get(`/gamelottery/top7.json?type=${type}`, config).then((res) => {
            if (res.data.code === 200) {
              const currentPeriod = this.Timer.bestbetssc && this.Timer[type].currentPeriod;
              const lastPeriod = res.data.data[0].period;
              if (parseInt(lastPeriod, 10) + 1 === parseInt(currentPeriod)) {
                this.historyNum[type] = res.data.data;
                clearInterval(this.lastLotteryTimers[type]);
              }
            }
          });
        }, 2000);
      },
      initLottery (type) {
        this.getTimer(type);
        this.getLotteryHistory(type);
      }
    }
  };
</script>
<style scoped>
  .index-main {
    width: 1200px;
    margin: 0 auto;
  }

  .index-border {
    border-radius: 8px;
    border: 1px solid #d5d5d5;
  }

  .index-carousel {
    background: url('../assets/images/banner.jpg');
  }

  .el-carousel {
    width: 100%;
  }

  .el-row {
    line-height: 2;
    margin-top: 10px;
    font-size: 1.2em;
  }

  .alarm {
    background: url(../assets/images/laba.jpg) no-repeat 10px, 10px;
    background-color: #f6f6f6;
    padding-left: 40px;
    line-height: 30px;
    border-radius: 6px;
  }

  .middle-container {
    width: 1200px;
    text-align: center;
    margin-top: 30px;
    border-radius: 8px;
    background: #ededed;
  }

  .middle-container > a {
    background: #c30;
    position: relative;
    top: -10px;
    padding: 10px 20px;
    font-size: 1.4em;
    color: #fff;
    border-radius: 6px;
    box-shadow: coral 0 0 2px;

  }

  .leftright2 {
    display: flex;
    width: 100%;
  }

  .left1 {
    width: 70%;
  }

  .ball {
    display: inline-block;
    width: 36px;
    line-height: 36px;
    margin-right: 4px;
    border-radius: 50%;
    background: #c30;
    color: #fff;
  }

  .history-hand {
    position: relative;
    cursor: pointer;
    padding: 8px;
    z-index: 100;
  }

  .left1 p {
    line-height: 3;
  }

  .left1 > div {
    display: inline-flex;
    padding: 10px;
  }

  .left1 > div > span {
    background: #c30;
    margin-right: 25px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-size: 38px;
    text-align: center;
    line-height: 53px;
    color: #fff;
  }

  .left1 p > span {
    padding-left: 10px;
    font-size: 1.5em;
  }

  .seriod {
    color: #c30;
  }

  .right1 {
    width: 30%;
  }

  .right1 > h3, .right1 > span {
    margin-top: 10px;
    font-size: 1.6em;
  }

  .right1 > span {
    display: inline-block;
    text-align: center;
    background: #5c5f70;
    padding: 12px 30px;
    color: #fff;
  }

  .airship .left1 > div > span {
    background: #2b9fa5;
  }

  .airship > a {
    background: #2b9fa5;
  }
</style>



