import Vue from 'vue';
import Router from 'vue-router';

import Index from '../pages/Index';
import About from '../pages/About';
import GameBack from '../pages/GameBack';
import Rules from '../pages/Rules';
import History from '../pages/History';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/gameback',
      name: 'GameBack',
      component: GameBack
    },
    {
      path: '/rules',
      name: 'Rules',
      component: Rules
    },
    {
      path: '/history',
      name: 'History',
      component: History
    }
  ]
});



