<template>
  <div id="app">
    <Header></Header>
    <router-view></router-view>
    <Footer></Footer>
  </div>
</template>

<script>
  import Header from './components/Header';
  import Footer from './components/Footer';

  export default {
    name: 'app',
    components: {
      Header,
      Footer
    }
  };
</script>
<style>
  body {
    font-family: "Helvetica Neue", Helvetica, sans-serif
  }

  .ball-num {
    background-color: #002951;
    width: 40px;
    line-height: 40px;
    border-radius: 50%;
    font-weight: bold;
    font-size: 1.2rem;
    color: #FFF;
    display: inline-block;
    text-align: center;
  }

  p .time-span {
    display: inline-block;
    width: 60px;
    line-height: 60px;
    background: linear-gradient(to bottom, #000 50%, gray 50%);
    color: #fff;
    font-size: 2rem;
    font-weight: bold;
  }
</style>



