/*
 *
 * 时间格式化过滤器
 *
 * Description
 *
 */

// 取剩余秒
const pluralize = (time, label) => `${time + label}前`;

// 相对时间过滤器，传入时间，返回距离今天有多久
export const timeAgo = (time) => {
  time = time instanceof Date ? time : new Date(time);
  const between = Date.now() / 1000 - (Number(time) / 1000); // eslint-disable-line
  if (between < 3600) {
    if (Object.is(~~(between / 60), 0)) return '刚刚'; // eslint-disable-line
    return pluralize(~~(between / 60), ' 分钟'); // eslint-disable-line
  } else if (between < 86400) {
    return pluralize(~~(between / 3600), ' 小时'); // eslint-disable-line
  }
  return pluralize(~~(between / 86400), ' 天'); // eslint-disable-line
};

// 转换为本地时间格式
export const toLocalString = date => date ? new Date(date).toLocaleString() : date; // eslint-disable-line

// YMDHMS时间转换过滤器
export const toYMD = (date) => {
  if (!date) return date;
  date = new Date(date);
  return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()} ${date.getHours() > 11 ? '下午' : '上午'}`;
};

// 将时间戳 times 转化为指定格式的String
// 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
// 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
// 例子：
// dateFormat(495646546954,'yyyy-MM-dd HH:mm:ss.S') ==> 2006-07-02 08:09:04.423
// dateFormat(494654654544,'yyyy-M-d H:m:s.S')      ==> 2006-7-2 8:9:4.18
export function dateFormat (times, fmt) {
  if (!times) {
    return '';
  }
  const d = new Date(times);
  const o = {
    'M+': d.getMonth() + 1, // 月份
    'd+': d.getDate(), // 日
    'H+': d.getHours(), // 小时
    'm+': d.getMinutes(), // 分
    's+': d.getSeconds(), // 秒
    'q+': Math.floor((d.getMonth() + 3) / 3), // 季度
    S: d.getMilliseconds() // 毫秒
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (String(d.getFullYear())).substr(4 - RegExp.$1.length));
  for (let k in o) //eslint-disable-line
    if (new RegExp(`(${k})`).test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length) === 1 ? (o[k]) : ((`00${o[k]}`).substr((String(o[k])).length)));
  return fmt;
}



